*{
    margin: 0;
    padding: 0;
}

.product_container {
    padding: 1rem;
}

@media only screen and (min-width: 1024px) {
    .product_container {
        padding: 1rem 4rem;
    }

    .product_header, .product_footer {
        padding: 0.5rem 4rem;
    }
    
    .product_footer{
        padding-top: 1.5rem;
    }

}