    font-family: sans-serif;
    display: block;
    width: 92%;
    font-size: 16px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);  
  }
  
.contactform_container button {
    padding: 0.5rem 6.2rem 0.5rem 6rem;
    margin: 1rem 0 0 1rem;
    border-radius: 5rem;
    background-color: rgb(238, 44, 109);
    color: #fff;
}



.contactform_radio_container {
    display: -webkit-flex;
    display: flex;
}
.contactform_radio {
    margin: 0.5rem;
  }
  .contactform_radio input[type=radio] {
    position: absolute;
    opacity: 0;
  }
  .contactform_radio input[type=radio] + .contactform_radio_label:before {
    content: "";
    background: #f4f4f4;
    border-radius: 100%;
    border: 1px solid #b4b4b4;
    display: inline-block;
    width: 1.4rem;
    height: 1.4em;
    position: relative;
    top: -0.2em;
    margin-right: 1em;
    vertical-align: top;
    cursor: pointer;
    text-align: center;
    transition: all 250ms ease;
  }
  .contactform_radio input[type=radio]:checked + .contactform_radio_label:before {
    background-color: #3197EE;
    box-shadow: inset 0 0 0 4px #f4f4f4;
  }
  .contactform_radio input[type=radio]:focus + .contactform_radio_label:before {
    outline: none;
    border-color: #3197EE;
  }
  .contactform_radio input[type=radio]:disabled + .contactform_radio_label:before {
    box-shadow: inset 0 0 0 4px #f4f4f4;
    border-color: #b4b4b4;
    background: #b4b4b4;
  }
  .contactform_radio input[type=radio] + .contactform_radio_label:empty:before {
    margin-right: 0;
  }

  .contactform_radio_label {
      width: 8rem !important;
  }

  @media only screen and (min-width: 767px) and (max-width: 1022px){
    .contactform_page {
      margin: 0rem 8rem 0.5rem 8rem;
    }

    .contactform_container {
      padding: 1rem 4rem 1rem 2rem;
  }
  .contactform_input {
    width: 100%
  }
  .contactform_container button {
    margin-left: 6rem;
  }
}
